<template>
<div>
  <table class="My_table">
    <tr>
      <th>主题</th>
      <th>时间</th>
      <th>地点</th>
      <th>操作</th>
    </tr>
    <tr v-for="(item, index) in data" :key="index">
      <td>{{item.theme}}</td>
      <td>{{item.data}}</td>
      <td>{{item.address}}</td>
      <td><a href="#"><i class="fa fa-pencil-square-o" aria-hidden="true"></i></a><span>&nbsp;||&nbsp;</span>
        <a href="#"><i class="fa fa-trash" aria-hidden="true"></i></a></td>
    </tr>
  </table>
</div>
</template>

<script>
export default {
  name: "voluntary",
  data (){
    return {
      data: '',
    }
  },
  methods: {
    getData (){
      const than = this
      than.axios.get('http://127.0.0.1:8000/volunteer/').then(
        res => {
          than.data = res.data
        }
      )
    }
  },
  created (){
    this.getData()
  }
}
</script>

